import React, { Component } from 'react';
import Link from './Link';
import PropTypes from 'prop-types';
import { constants } from '../Constants';
import axios from "axios";
import ArcTextField from '../Ux/ArcTextField';
import ArcDialog from '../Ux/ArcDialog';
import ViewResolver from '../Ux/ViewResolver';
import View from '../Ux/View';
import './style.scss';
import { Switch } from '@material-ui/core';
import { isEmptyOrSpaces, match } from '../Utils';
import Sidebar from '../Ux/Sidebar';

const queryString = require('query-string');
const baseUrl = process.env.REACT_APP_API_URL;

class Bookmarks extends Component {
    constructor(props) {
        super(props);
        this.state = {
            items: [],
            view: [],
            isEditDialogOpen: false,

            searchtext: '',
            isFiltered: false,

            id: null,
            title: '',
            href: '',
            tags: '',
            editDialogLabel: 'Add',
            firstLoad: true,

            searchPref: {
                title: true,
                tags: true,
                href: true
            },

            sidebarElements: {
                addNew: [
                    {
                        label: 'New Bookmark',
                        action: this.toggleEditDialog,
                        icon: 'note_add'
                    }
                ]
            }
        }
    }
    componentDidMount() {
        if (this.props.location.search) {
            const query = queryString.parse(this.props.location.search);
            if (query && query.q) {
                if (query.q.startsWith('tags')) {
                    this.setState({
                        searchPref: {
                            title: false,
                            tags: true,
                            content: false
                        }
                    })
                }
                this.setState({
                    searchtext: query.q,
                    isFiltered: true
                })
            }
        }

        if(this.state.firstLoad && this.props.authorization.isAuth) {
            this.initializeBookmarks(this.props.authorization);
            this.setState({firstLoad: false})
        }
    }
    componentWillReceiveProps(nextProps) {
        if (this.state.firstLoad && nextProps.authorization) {
            this.initializeBookmarks(nextProps.authorization);
            this.setState({firstLoad: false})
        }
    }

    initializeBookmarks(authorization) {
        const that = this;
        axios.get(baseUrl + constants.API_URL_BOOKMARK,
            {
                headers: {
                    Authorization: 'Bearer ' + authorization.token
                }
            })
            .then(function(response) {
                that.setState({items: response.data, view: response.data});
                if (that.state.isFiltered) {
                    that.search();
                }
            }
        );
    }

    toggleEditDialog = () => {
        this.setState({
            isEditDialogOpen: !this.state.isEditDialogOpen,
            id: null,
            title: '',
            href: '',
            tags: '',
            editDialogLabel: 'Add'
        })
    }

    editBookmark = (bookmark) => {
        this.setState({
            isEditDialogOpen: true,
            id: bookmark._id,
            title: bookmark.title,
            href: bookmark.href,
            tags: bookmark.tags,
            editDialogLabel: 'Save'
        })
    }

    deleteBookmark = (bookmarkId) => {
        const that = this;
        axios.delete(baseUrl + constants.API_URL_BOOKMARK + "/" + bookmarkId,
        {
            headers: {
                Authorization: 'Bearer ' + this.props.authorization.token
            }
        })
        .then(function(response) {
            if (response.status === 201) {
                that.props.sendEvent('notification', true, {type: 'success', message: 'Bookmark deleted', duration: 5000});
                that.initializeBookmarks(that.props.authorization);
            }
        })
        .catch((error) => {
            if (error.response.status === 401) {
                that.props.logout(null, 'failure', 'Session expired. Login again');
            }
        })
    }

    clearSearch = () => {
        this.setState({
            view: this.state.items,
            isFiltered: false,
            searchtext: ''
        })
        this.props.sendEvent('sidebar', false)
    }

    search = (event) => {
        if (event) {
            event.preventDefault();
        }

        if (isEmptyOrSpaces(this.state.searchtext)) {
            this.setState({
                view: this.state.items,
                isFiltered: false
            });
            return;
        }

        this.setState({
            view: this.state.items.filter((item) => {
                if (this.state.searchPref.title && match(item.title, this.state.searchtext)) {
                    return true;
                } else if (this.state.searchPref.tags && match(item.tags, this.state.searchtext)) {
                    return true;
                } else if (this.state.searchPref.href && match(item.href, this.state.searchtext)) {
                    return true;
                }
            }),
            isFiltered: true
        });
        this.props.sendEvent('sidebar', false)
    }

    toggleSearchPref = (pref) => {
        this.setState({
            searchPref: {
                ...this.state.searchPref,
                [pref]: !this.state.searchPref[pref]
            }
        })
    }

    addBookmark= () => {
        const that = this;

        let bookmark = {
            id: this.state.id,
            title: this.state.title,
            href: this.state.href,
            tags: this.state.tags
        }

        if (isEmptyOrSpaces(bookmark.title)) {
            that.props.sendEvent('notification', true, {type: 'failure', message: 'Title / description missing', duration: 5000});
            return;
        }

        if (isEmptyOrSpaces(bookmark.href)) {
            that.props.sendEvent('notification', true, {type: 'failure', message: 'Website URL / Link is missing', duration: 5000});
            return;
        }

        if (isEmptyOrSpaces(bookmark.tags)) {
            bookmark.tags = 'unsorted';
        }

        axios.put(baseUrl + constants.API_URL_BOOKMARK, bookmark,
        {
            headers: {
                Authorization: 'Bearer ' + this.props.authorization.token
            }
        })
        .then(function(response) {
            if (response.status === 201) {
                that.props.sendEvent('notification', true, {type: 'success', message: 'Bookmark created', duration: 5000});
                that.toggleEditDialog();

                that.initializeBookmarks(that.props.authorization);
            }
        })
        .catch((error) => {
            if (error.response.status === 401) {
                that.props.logout(null, 'failure', 'Session expired. Login again');
            }
        })
    }

    handleChange = (event) => {
        this.setState(
            {
                [event.currentTarget.name]: event.currentTarget.value
            }
        )
    }

    render() {
        const listview = this.state.view.map(item => (
            <div key={item._id}>
            <Link id={item._id} bookmark={item} editBookmark={this.editBookmark} deleteBookmark={this.deleteBookmark}/>
            <br />
            </div>
        ))
        return (
            <div className="bookmarks">
                <ArcDialog title="Add Bookmark" visible={this.state.isEditDialogOpen} toggleVisibility={this.toggleEditDialog}>
                    <ArcTextField label="Title" data={this.state} id="title" handleChange={e => this.handleChange(e)} />
                    <ArcTextField label="URL" data={this.state} id="href" handleChange={e => this.handleChange(e)} />
                    <ArcTextField label="Tags" data={this.state} id="tags" handleChange={e => this.handleChange(e)} />
                    <div className="actions">
                        <button onClick={this.toggleEditDialog} className="default disabled"><i className="material-icons">close</i>Cancel</button>
                        <button onClick={this.addBookmark} className="primary block"><i className="material-icons">double_arrow</i>{this.state.editDialogLabel}</button>
                    </div>
                </ArcDialog>

                <ViewResolver event={this.props.event} sendEvent={this.props.sendEvent}>
                    <View main>
                        {listview}
                    </View>
                    <View side>
                        <div className="filter-container">
                            <div className="section-main">
                                <Sidebar label="Add New" elements={this.state.sidebarElements['addNew']} icon="add" animate />
                                <Sidebar label="Search" elements={this.state.sidebarElements['search']} icon="search" animate number={this.state.isFiltered ? this.state.view.length : undefined}>
                                    <form method="GET" onSubmit={this.search} noValidate>
                                    <div className="space-top-2 space-left-4 space-right-4"><ArcTextField label="Keywords" id="searchtext" data={this.state} handleChange={e => this.handleChange(e)} /></div>
                                    </form>
                                    <div className="typography-5 space-top-2 space-left-4">
                                        <Switch
                                            checked={this.state.searchPref.title}
                                            onChange={() => this.toggleSearchPref('title')}
                                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                                        Include title
                                    </div>
                                    <div className="typography-5 space-top-2 space-left-4">
                                        <Switch
                                            checked={this.state.searchPref.tags}
                                            onChange={() => this.toggleSearchPref('tags')}
                                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                                        Include tags
                                    </div>
                                    <div className="typography-5 space-top-2 space-left-4">
                                        <Switch
                                            checked={this.state.searchPref.href}
                                            onChange={() => this.toggleSearchPref('href')}
                                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                                        Include URL
                                    </div>
                                    {this.state.isFiltered && <div className="typography-4 space-top-2 space-left-2">Found {this.state.view.length} bookmarks matching the search criteria</div>}
                                    <div className="actionbar-2 space-top-2 space-bottom-2">
                                        <div>
                                            <button onClick={this.clearSearch} className="default">Clear</button>
                                        </div>
                                        <div>
                                            <button onClick={this.search} className="default animate space-right-2">Search</button>
                                        </div>
                                    </div>

                                </Sidebar>
                            </div>
                        </div>
                    </View>
                </ViewResolver>
            </div>
        )
    }
}

Bookmarks.propTypes = {
    receiveEvents: PropTypes.func.isRequired,
    sendEvent: PropTypes.func.isRequired,
}

export default Bookmarks;
